import { Component } from "react";
import Header from './Header';
import lang from "../language/lang";
import axios from 'axios';
const baseurl = process.env.REACT_APP_API_BASE_URL;
class NotificationDetail extends Component{
    constructor(props){
        super(props);
        this.state={
            newsList:[],
            id:"",
            news:{
                title_jp:"",
                content_jp:"",
                title_en:"",
                content_en:"",
                date:""
            },
            language:JSON.parse(localStorage.language).language
        }
    }

    componentDidMount(){
        const {id} = this.props.match.params;
        console.log(id)
       this.getnewsData(id)
    }

    getnewsData(id){
        var userData =  JSON.parse(localStorage.getItem("userData")) || null;
        var token = userData.token
        var config = {
        method: 'get',
        url: `${baseurl}/api/news/${id}`,
        headers: { 
            'Content-Type': 'application/json',
            'Authorization': 'Bearer ' + token,
        },
        data : {}
        };
        axios(config)
        .then(async (response)=>{
            const {title_jp,content_jp,title_en,content_en,created_at} = response.data.news
            var news={
                title_jp:title_jp,
                content_jp:content_jp,
                title_en:title_en,
                content_en:content_en,
                date:created_at
            }
            this.setState({
                news:news
            })
            
        })
        .catch((error)=>{
            if (error.response) {
                if(error.response.status==401){
                    localStorage.removeItem("userData");
                    window.location.assign('/');
                }
            }        
        });
    }
    handleLogout = (event)=>{
        localStorage.removeItem("adminData");
        window.location.assign('/');
    }

    langClickSp = event=> {
        var langSelect = document.getElementsByClassName('header-lang-select-sp');
        if (langSelect[0].style.maxHeight) {
            langSelect[0].style.maxHeight = null;
        }
        else {
            langSelect[0].style.maxHeight = langSelect[0].scrollHeight + "px";
        }
    }

    langSelectSp = x => event => {
        var langSelect = document.getElementsByClassName('header-lang-select-sp');
        if (langSelect[0].style.maxHeight) {
            langSelect[0].style.maxHeight = null;
        }
        localStorage.setItem("language", JSON.stringify({language:x}))
        this.setState({language:x});
        window.location.reload(); 
    }
    render()
    {
        const{news, language}=this.state
        return(
            <>
               <main id="notification">
                    <header>
                        <h2>{lang[language].news}</h2>
                    </header>
                    <section className={language==="jp"?"container-main":"container-main container-main-en"}>
                        <Header/>
                        <div class="container1">
                            <div class="crumb">
                            
                                <p><a href="/dashboard/home"> {lang[language].top}</a> &nbsp;&gt;&nbsp; <a href="/dashboard/notification">{lang[language].news}</a> &nbsp;&gt;&nbsp; {news[`title_${language}`]}</p>
                                <div className="header-lang-sp" onClick={this.langClickSp}>
                                    <div  className={language==="jp"? "header-lang-btn-sp header-lang-jp":"header-lang-btn-sp header-lang-us"}></div>
                                    <div className="header-lang-arrow-sp"></div>
                                    <div className="header-lang-select-sp" onClick={(e)=>{e.stopPropagation();}}>
                                        <div>
                                            <div onClick={this.langSelectSp('jp')}> <img src="/assets/img/icon_lang_jpn.png" alt=""/></div>
                                            <div onClick={this.langSelectSp('en')}><img src="/assets/img/icon_lang_usa.png" alt=""/></div>
                                        </div>
                                    </div>
                                </div>
                                <a onClick={this.handleLogout} className="logout-sp">{lang[language].log_out}</a>
                            </div>
                            <div class="notification-container">
                                <div class="notification-detail-box">
                                    <h3>{(news.date).substring(0, 10)}</h3>
                                    <h4>{news[`title_${language}`]}</h4>
                                    <p dangerouslySetInnerHTML={{__html: news[`content_${language}`]}}>
                                    </p>
                                </div>
                            </div>
                        </div>
                    </section>
                </main>    
            </>
        )
    }
}

export default NotificationDetail;