import { Component } from "react";
import Header from './Header';
import axios from 'axios';
import lang from "../language/lang";
const baseurl = process.env.REACT_APP_API_BASE_URL;

class Profile extends Component{
    constructor(props)
    {
        super(props)
        this.state={
            userName:"",
            academy:"",
            memberID:"",
            country:"",
            email:"",
            walletaddress:"",
            language:JSON.parse(localStorage.language).language
        }
    }

    componentDidMount(){
        this.getProfile();
    }

    getProfile(){
        var userData =  JSON.parse(localStorage.getItem("userData")) || null;
        var token = userData.token
        var config = {
            method: 'get',
            url: `${baseurl}/api/getProfile`,
            headers: { 
              'Content-Type': 'application/json',
              'Authorization': 'Bearer ' + token,
            },
            data : {}
          };
          axios(config)
          .then(async (response)=>{
                var userData= response.data.user;
                console.log(userData)
                this.setState({
                    userName:userData.name,
                    academy:userData.academy,
                    memberID:userData.memberID,
                    country:userData.country,
                    email:userData.email,
                    walletaddress:userData.walletaddress
                })
           })
          .catch((error)=>{
           });
    }
    handleLogout = (event)=>{
        localStorage.removeItem("adminData");
        window.location.assign('/');
    }

    langClickSp = event=> {
        var langSelect = document.getElementsByClassName('header-lang-select-sp');
        if (langSelect[0].style.maxHeight) {
            langSelect[0].style.maxHeight = null;
        }
        else {
            langSelect[0].style.maxHeight = langSelect[0].scrollHeight + "px";
        }
    }

    langSelectSp = x => event => {
        var langSelect = document.getElementsByClassName('header-lang-select-sp');
        if (langSelect[0].style.maxHeight) {
            langSelect[0].style.maxHeight = null;
        }
        localStorage.setItem("language", JSON.stringify({language:x}))
        this.setState({language:x});
        window.location.reload(); 
    }
    render()
    {
        const{userName, academy, memberID, country, email, walletaddress,language} = this.state
        return(
            <>
               <main id="information">
                    <header>
                        <h2>{lang[language].profile}</h2>
                    </header>
                    <section className={language==="jp"?"container-main":"container-main container-main-en"}>
                        <Header/>
                        <div className="container1">
                            <div className="crumb">
                                <p><a href="/dashboard/home">{lang[language].top}</a> &nbsp;&gt;&nbsp; {lang[language].profile}</p>
                                <div className="header-lang-sp" onClick={this.langClickSp}>
                                    <div  className={language==="jp"? "header-lang-btn-sp header-lang-jp":"header-lang-btn-sp header-lang-us"}></div>
                                    <div className="header-lang-arrow-sp"></div>
                                    <div className="header-lang-select-sp" onClick={(e)=>{e.stopPropagation();}}>
                                        <div>
                                            <div onClick={this.langSelectSp('jp')}> <img src="/assets/img/icon_lang_jpn.png" alt=""/></div>
                                            <div onClick={this.langSelectSp('en')}><img src="/assets/img/icon_lang_usa.png" alt=""/></div>
                                        </div>
                                    </div>
                                </div>
                                <a onClick={this.handleLogout} className="logout-sp">{lang[language].log_out}</a>
                            </div>
                            <div className="information-container">
                                <div className="information-box">
                                    <h4>{lang[language].userName}</h4>
                                    <p>{userName}</p>
                                </div>
                                <div className="information-box">
                                    <h4>{lang[language].academy}</h4>
                                    <p>{academy}</p>
                                </div>
                                <div className="information-box">
                                    <h4>{lang[language].memberID}</h4>
                                    <p>{memberID}</p>
                                </div>
                                <div className="information-box">
                                    <h4>{lang[language].country}</h4>
                                    <p>{country}</p>
                                </div>
                                <div className="information-box">
                                    <h4>{lang[language].registeremail}</h4>
                                    <p>{email}</p>
                                </div>
                                <div className="information-box">
                                    {lang[language].walletAddress}
                                    <p>{walletaddress}</p>
                                </div>
                            </div>
                            <div className="information-container">
                                <a href="/dashboard/profile/edit" className="yellow-btn information-btn">
                                    <img src="/assets/img/icon_information.png" alt="" />
                                    <span> {lang[language].changeInforbtn}</span>
                                </a>
                            </div>
                        </div>
                    </section>
                </main>
            </>
        )
    }
}

export default Profile;