import { Component } from "react";
import axios from 'axios';
import lang from "../language/lang";
const baseurl = process.env.REACT_APP_API_BASE_URL;
const academyList = {
    jp:[
        "アカデミ",
        "アカデミ",
        "アカデミ"
    ],
    en:[
        "Risedot",
        "Risedot",
        "Risedot"
    ]
}

class Mypage extends Component{

    constructor(props)
    {
        super(props)
        this.state={
            userName:"",
            academy:"",
            daypoint:0,
            totalpoint:0,
            point:0,
            weekpoint:0,
            monthpoint:0,
            lastdate:"00",
            lasttime:"00",
            lastminute:"00",
            lastsecond:"00",
            avatarimg:"",
            history:[],
            packagename:"",
            language:JSON.parse(localStorage.language).language,
        }
    }

    componentDidMount(){
        this.getProfile();
        this.getPointHistory(0)
    }
    
    componentWillUnmount() {
        clearInterval(this.interval)
    }

    getProfile(){
        var loginuserData =  JSON.parse(localStorage.getItem("userData")) || null;
        var token = loginuserData.token
        var config = {
            method: 'get',
            url: `${baseurl}/api/getProfile`,
            headers: { 
              'Content-Type': 'application/json',
              'Authorization': 'Bearer ' + token,
            },
            data : {}
          };
          axios(config)
          .then(async (response)=>{
            var userData= response.data.user
            this.setState({
                userName:userData.name,
                academy:userData.academy,
                packagename:userData.package,
                avatarimg:userData.avatar?`${baseurl}/media/${userData.avatar}`:"",
                totalPoint:(Math.round(userData.point * 100) / 100).toFixed(2),
                daypoint:(Math.round(userData.daypoint * 100) / 100).toFixed(2),
                point:parseInt(userData.point).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                weekpoint:(Math.round(userData.weekpoint * 100) / 100).toFixed(2),
                monthpoint:(Math.round(userData.monthpoint * 100) / 100).toFixed(2),
            })
            console.log(userData.lastdate)
            this.getLastDate((userData.lastdate).replace("(","").replace(")","").replace(",",""))
            this.interval = setInterval(() => this.getLastDate((userData.lastdate).replace("(","").replace(")","").replace(",","")), 1000);              
           })
          .catch((error)=>{
                if (error.response) {
                    if(error.response.status===401){
                        localStorage.removeItem("userData");
                        window.location.assign('/');
                    }
                }
           });
    }

    getPointHistory(offset)
    {
        var data = JSON.stringify({"offset":offset,"limit":10});
        var userData =  JSON.parse(localStorage.getItem("userData")) || null;
        var token = userData.token
        var config = {
            method: 'post',
            url: `${baseurl}/api/pointhistory`,
            headers: { 
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + token
            },
                data : data,
        };
        axios(config)
        .then((response)=>{
            var history = response.data.history
            this.setState({history:history})
        })
        .catch((error)=>{

        })
    }


    componentWillUnmount() {
        clearInterval(this.interval)
    }

    getLastDate(date){
        var lastdate = new Date(date)
        var today = new Date();
        const diff = lastdate - today;
        if(diff<0)
        {
            return;
        }
        else{
            var seconds = Math.floor(diff / 1000);
            var date = parseInt(seconds / (3600 * 24))
            seconds = seconds % (3600 * 24)
            var time = parseInt(seconds / 3600);
            seconds = seconds % 3600
            var minutes = parseInt(seconds / 60)
            seconds = seconds % 60
            this.setState({
                lastdate:date,
                lasttime:time,
                lastminute:minutes,
                lastsecond:seconds
            })
        }
    }

    updateAvatar = e =>{
        let fileObj = e.target.files[0];
        let avatar = URL.createObjectURL(fileObj);
        const fd = new FormData();
        fd.append('avatar', fileObj);
        var userData =  JSON.parse(localStorage.getItem("userData")) || null;
        var token = userData.token
        axios.post(`${baseurl}/api/updateAvatar`, fd, {
            headers: {
                'Authorization': 'Bearer ' + token,
            }
        }).then((response)=>{
        }).catch((error)=> {
        })      
        this.setState({
            avatarimg:avatar,
        })
    }

    langClick = event=> {
        var langSelect = document.getElementsByClassName('header-lang-select');
        if (langSelect[0].style.maxHeight) {
            langSelect[0].style.maxHeight = null;
        }
        else {
            langSelect[0].style.maxHeight = langSelect[0].scrollHeight + "px";
        }
    }

    langSelect = x => event => {
        var langSelect = document.getElementsByClassName('header-lang-select');
        if (langSelect[0].style.maxHeight) {
            langSelect[0].style.maxHeight = null;
        }
        localStorage.setItem("language", JSON.stringify({language:x}))
        this.setState({language:x})
        window.location.reload(); 
    }

    handleLogout = (event)=>{
        localStorage.removeItem("adminData");
        window.location.assign('/');
    }

    langClickSp = event=> {
        var langSelect = document.getElementsByClassName('header-lang-select-sp');
        if (langSelect[0].style.maxHeight) {
            langSelect[0].style.maxHeight = null;
        }
        else {
            langSelect[0].style.maxHeight = langSelect[0].scrollHeight + "px";
        }
    }

    langSelectSp = x => event => {
        var langSelect = document.getElementsByClassName('header-lang-select-sp');
        if (langSelect[0].style.maxHeight) {
            langSelect[0].style.maxHeight = null;
        }
        localStorage.setItem("language", JSON.stringify({language:x}))
        this.setState({language:x});
        window.location.reload(); 
    }

   
    render()
    {
        const {userName, academy, daypoint, weekpoint, monthpoint, point, lastdate, lasttime, lastminute, lastsecond, avatarimg, language, totalPoint, packagename} = this.state
        return(
            <>
              <main>
                <header>
                    <h2>{lang[language].myPage}</h2>
                    
                </header>
                <section className={language==="jp"?"container-main":"container-main container-main-en"}>
                    <div className="header">
                        <div className="header-img">
                            <img onClick={(e) => this.upload.click() } src={avatarimg=="" ? "/assets/img/avatar.jpg" : avatarimg} alt="" />
                            <input type="file" onChange={this.updateAvatar} ref={(ref) => this.upload = ref} style={{ display: 'none' }} multiple accept="image/png, image/gif, image/jpeg"/>
                        </div>
                        <table className="header-table">
                            <tbody>
                            <tr>
                                <td>{lang[language].userName}</td>
                                <td>{userName}</td>
                            </tr>
                            <tr>
                                <td>{lang[language].academy}</td>
                                <td>{academy}</td>
                            </tr>
                            <tr>
                                <td>{lang[language].package}</td>
                                <td>{packagename}</td>
                            </tr>
                            <tr>
                                <td>{lang[language].ownedPoint}</td>
                                <td>{point}</td>
                            </tr>
                            </tbody>
                        </table>
                        <div className="header-lang" onClick={this.langClick}>
                            <div className={language==="jp"?"header-lang-btn header-lang-jp":"header-lang-btn header-lang-us"}></div>
                            <div className="header-lang-arrow"></div>
                            <div className="header-lang-select" onClick={(e)=>{e.stopPropagation();}}>
                                <div>
                                    <div onClick={this.langSelect('jp')}><img src="/assets/img/icon_lang_jpn.png" alt=""/></div>
                                    <div onClick={this.langSelect('en')}><img src="/assets/img/icon_lang_usa.png" alt=""/></div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="container1">
                        <div className="crumb">
                            <p><a href="">{lang[language].top}</a> &nbsp;&gt;&nbsp; {lang[language].myPage}</p>
                            <div className="header-lang-sp" onClick={this.langClickSp}>
                                <div  className={language==="jp"? "header-lang-btn-sp header-lang-jp":"header-lang-btn-sp header-lang-us"}></div>
                                <div className="header-lang-arrow-sp"></div>
                                <div className="header-lang-select-sp" onClick={(e)=>{e.stopPropagation();}}>
                                    <div>
                                        <div onClick={this.langSelectSp('jp')}> <img src="/assets/img/icon_lang_jpn.png" alt=""/></div>
                                        <div onClick={this.langSelectSp('en')}><img src="/assets/img/icon_lang_usa.png" alt=""/></div>
                                    </div>
                                </div>
                            </div>
                            <a onClick={this.handleLogout} className="logout-sp">{lang[language].log_out}</a>
                        </div>
                        <div className="top-container">
                            <div className="point-info">
                                <svg xmlns="http://www.w3.org/2000/svg" version="1.1" fill="transparent" style={{overflow:"visible"}}>
                                    <rect rx="15" ry="15" height="100%" width="100%" stroke="white" />
                                </svg>
                                <div className="point-info-title">
                                    <h3 className="point-info-title-spec">{lang[language].totalPoint}</h3>
                                </div>
                                <div className="point-info-main">
                                    <h4>{totalPoint}<span>{lang[language].point}</span></h4>
                                </div>
                            </div>
                            <div className="point-info-detail">
                                <div>
                                    <h3>
                                        <span>{lang[language].month}</span><span>{lang[language].earnedPoint}</span>
                                    </h3>
                                    <h4>{monthpoint}</h4>
                                    <p>{lang[language].point}</p>
                                </div>
                                <div>
                                    <h3>
                                        <span>{lang[language].week}</span><span>{lang[language].earnedPoint}</span>
                                    </h3>
                                    <h4>{weekpoint}</h4>
                                    <p>{lang[language].point}</p>
                                </div>
                                <div>
                                <h3>
                                    <span>{lang[language].day}</span><span>{lang[language].earnedPoint}</span>
                                    </h3>
                                    <h4>{daypoint}</h4>
                                    <p>{lang[language].point}</p>
                                </div>
                            </div>

                        </div>
                    </div>
                    <div className="mypage-container">
                        <div className="container1">
                            <div className="remaining">
                                <h3>{lang[language].courseRemain}</h3>
                                <p><span>{lastdate}</span>{lang[language].days}<span>{lasttime}</span>{lang[language].hrs}<span>{lastminute}</span>{lang[language].min}<span>{lastsecond}</span>{lang[language].sec}</p>
                            </div>
                        </div>
                        <div className="point-history">
                            <h3>{lang[language].pointHistory}</h3>
                            <div className="point-history-tab-container">
                                <div className="point-history-tab point-history-tab-active" >{lang[language].exchangeHistory}</div>
                                <div className="point-history-tab"  >
                                    {/* {lang[language].transactionHistory} */}
                                </div>
                            </div>
                            <div className="point-history-content-container point-history-content-container-show" id="point-container1">
                                <div className="point-history-content">
                                    <div className="point-history-content-title">
                                        <div>{lang[language].date}</div>
                                        <div>{lang[language].transactionContent}</div>
                                        <div>{lang[language].point}</div>
                                    </div>
                                    {this.state.history.map((item) => (
                                        item.stakingpoint!=0 && <div className="point-history-content-desc" key={item.pk}>
                                            <div>{`${item.pointdate.substring(0,4)}${lang[language].year_label}${item.pointdate.substring(5,7)}${lang[language].month_label}${item.pointdate.substring(8,10)}${lang[language].day_label}`}</div>
                                            <div>{`${lang[language].point_receptance_history}${item.pointdate.substring(0,4)}${lang[language].year_label}${item.pointdate.substring(5,7)}${lang[language].month_label}${item.pointdate.substring(8,10)}${lang[language].day_label}${(Math.round(item.stakingpoint * 100) / 100).toFixed(2)}${lang[language].earned_point}`}</div>
                                            <div><span>{(Math.round(item.stakingpoint * 100) / 100).toFixed(2)}</span>{lang[language].point}</div>
                                        </div>
                                    ))}
                                </div>
                            </div>
                            <div className="point-history-content-container" id="point-container2">
                            </div>
                        </div>
                    </div>
                </section>
            </main>
            </>
        )
    }
}

export default Mypage;