import React from 'react';
import {BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import Top from './component/Top';
import Login from './component/Login';
import Register from './component/Register';
import ResetPassword from './component/ResetPassword';
import Privacy from './component/Privacy';
import ResigerComplete from './component/ResigerComplete';
import Dashboard from './component/Dashboard';
import EmailVerify from './component/EmailVerify';

class Routes extends React.Component {
  
    render() {
       var userData =  JSON.parse(localStorage.getItem("userData")) || null;
      return (
          <Router>
              <Switch>
                  <Route exact from="/" component={Top} />
                  <Route exact from="/login" component={Login} />
                  <Route exact from="/register" component={Register} />
                  <Route exact from="/privacy" component={Privacy} />
                  <Route exact from="/resetPassword" component={ResetPassword} />
                  <Route exact from="/registersucess" component={ResigerComplete} />
                  <Route exact path='/emailverify/:token' component={EmailVerify}/>
                  <Route exact path='/dashboard/'>
                        {userData?<Redirect to="dashboard/home" />:<Redirect to="/"/>}
                  </Route> 
                  <Route exact path='/dashboard/*'>
                    {userData?<Dashboard/>:<Redirect to="/"/>}
                  </Route>

                  <Route
                    render={function() {
                        return <h1>Not Found</h1>;
                    }}
                  />
               </Switch>
          </Router>
   
      );
    }
  }
  
export default Routes;