import { Component } from "react";
import axios from 'axios';
import lang from "../language/lang";
const baseurl = process.env.REACT_APP_API_BASE_URL;

class EmailVerify extends Component{
    constructor(props){
        super(props);
        this.state={
            message:"",
            language:JSON.parse(localStorage.language).language
        }
    }
    componentDidMount()
    {
        var {token} = this.props.match.params;
        this.verifyEmail(token);
    }

    verifyEmail(token)
    {
        const {language} = this.state
        var config = {
            method: 'get',
            url: `${baseurl}/api/emailverify`,
            params: { token: token }
        };
        axios(config)
        .then((response) => {
            this.setState({
                message:lang[language].complete_register
            })
        })
        .catch((error)=>{
            if (error.response) {
                if(error.response.status===401){
                   this.setState({
                       message:lang[language].invalid_link
                   })
                }
                if(error.response.status===401){
                    this.setState({
                        message:lang[language].expired_link
                    })
                }
            }
        })
    }

    render()
    {
        const{language, message}=this.state
        return(
            <>
                <div className="logo sp">
                    <img src="./assets/img/bg_logo_black.png" alt="" />
                </div>
                <main id="registerComplete">
                    <header>
                        <h2>{lang[language].signup}</h2>
                    </header>
                    <section className="container">
                        <img src="./assets/img/bg_logo.png" alt="" className="pc" />
                        <h4>
                            <br /><br/>
                            {message}
                            <br/>
                        </h4>
                        <br/>
                        <a href="/login" className="white-btn">{lang[language].backlogin}</a>
                    </section>
                </main>
            </>
        )
    }
}

export default EmailVerify;