import { Component } from "react";
import lang from "../language/lang";
import axios from 'axios';
const baseurl = process.env.REACT_APP_API_BASE_URL;

class ResetPassword extends Component{
    
    constructor(props){
        super(props);
        this.state={
            email:"",
            erroremail:"",
            alertmodal:false,
            language:localStorage.getItem("language") === null ? "en" : JSON.parse(localStorage.language).language
        }
    }

    handleChange = filedName => e=>{
        this.setState({
            erroremail:"",
            [filedName]:e.target.value
        })
    }

    handleResetPassword = (event)=>{
        const {email} = this.state;
        var data = JSON.stringify({"email":email});
        var config = {
            method: 'post',
            url: `${baseurl}/api/resetPassword`,
            headers: { 
              'Content-Type': 'application/json'
            },
            data : data
          };
          axios(config)
          .then((response)=>{
            this.setState({
                alertmodal:true
            })
           })
          .catch((error)=>{
              
           });
    }

    render()
    {
        const{language,email,erroremail,alertmodal}=this.state
        return(
            <>
                <div className="logo sp">
                    <img src="./assets/img/bg_logo_black.png" alt="" />
                </div>
                <main>
                    <header>
                        <h2>{lang[language].passwordreset}</h2>
                    </header>
                    <section className="container">
                        <img src="./assets/img/bg_logo.png" alt="" className="pc" />
                        <div className="input-box">
                            <label for="">{lang[language].emailAddress}</label>
                            <input type="email" value={email} onChange={this.handleChange("email")}/>
                            <span className="error">{erroremail}</span>
                        </div>
                        <a className="yellow-btn" onClick={this.handleResetPassword}>
                            <img src="./assets/img/icon_reset.png" alt="" />
                            <span>{lang[language].reset}</span>
                        </a>
                        <a className="white-btn" href="/login">
                            <span>{lang[language].backlogin}</span>
                        </a>
                    </section>
                </main>
                <div className={alertmodal?"modal modal-show":"modal"} onClick={(e)=>{this.setState({alertmodal:false})}}>
                    <div className="modal-body" onclick="event.stopPropagation()">
                        <p>{language=="jp"?"再設定用のメールを送信しました。":"I sent an email for resetting."}</p>
                    </div>
                </div>
            </>
        )
    }
}

export default ResetPassword;