import { Component } from "react";
import lang from "../language/lang";
class Privacy extends Component{
    constructor(props){
        super(props);
        this.state={
            language:localStorage.getItem("language") === null ? "en" : JSON.parse(localStorage.language).language
        }
    }
    render()
    {
        const{language}=this.state
        return(      
            <main id="privacy">
            <header>
                <h2>{lang[language].terms}</h2>
            </header>
            <section className="container">
                <div className="privacy-container">
                    <div className="privacy-title"><h4>Terms of Use</h4></div>
                    <div className="privacy-content">
                        <h5>Article 1| Purpose</h5>
                        <p>1. Terms of Use of Connect Hearts Login Page and Point (hereinafter referred to as "Terms of Use") stipulate the terms of use of points issued by Connect Hearts (hereinafter referred to as "CH").</p>
                        <p>2. Help and guide pages related to points other than this agreement shall also part of this agreement, and the user agrees to this.</p>
                        <p>3. The user can use points after agreeing to this agreement.</p>
                        <p>4. This agreement shall be applied in combination with the membership agreement and other terms of use established by CH.</p>

                        <h5>Article 2| Purchase</h5>
                        <p>1. Points will be provided to users who purchased CH products or member of associated school with CH.</p>
                        <p>2. If the user is a minor, points or user account shall be managed with the consent of the legal representative.</p>

                        <h5>Article 3| Available services</h5>
                        <p>The points can be used for exchanging products and gifts. The lineup will be changed time to time without notice and for the services which points can be used.</p>

                        <h5>Article 4| Confirmation of available balance</h5>
                        <p>TPlease check the available balance of points from My Page after logging in to the member page.</p>

                        <h5>Article 5| Expiration date</h5>
                        <p>1. Please check from My Page after logging in to the member page about the expiration date of point and the member account.</p>
                        <p>2. Points will expire after the expiration date, regardless of the available balance.</p>

                        <h5>Article 6| Refunds, etc.</h5>
                        <p>1. Points are provided as token of our appreciation and service from CH and we will not accept returns, refunds or any complaints in this service.</p>
                        <p>2. CH will not reissue or redeem points for any reason.</p>

                        <h5>Article 7| Suspension / expiration of use</h5>
                        <p>CH may suspend the use of points or revoke points if any of the following items apply.</p>
                        <p>&nbsp;&nbsp;(1) When the user acquires points by an illegal method.</p>
                        <p>&nbsp;&nbsp;(2) When the user violates this agreement</p>
                        <p>&nbsp;&nbsp;(3) When the points are forged or altered</p>
                        <p>&nbsp;&nbsp;(4) When the CH determines that there is a considerable reason for the suspension of the use of points.</p>

                        <h5>Article 8| Suspension of use</h5>
                        <p>CH may suspend the use of points without prior notice to the user due to maintenance / inspection of the point management system, or due to a failure, natural disaster, communication line failure, or other reasons.</p>
                        
                        <h5>Article 9| Termination of service</h5>
                        <p>CH may issue or terminate the use of points due to changes in social conditions, revision or abolition of laws and regulations, or unavoidable reasons of CH.</p>

                        <h5>Article 10| Prohibition of transfer, etc.</h5>
                        <p>The user shall not be able to transfer, lend, or pledge the points to another person regardless of the method.</p>

                        <h5>Article 11| Limited liability</h5>
                        <p>CH shall not be liable for any damage or disadvantage caused to the user due to the inability to use the points. However, this shall not apply if the damage or disadvantage is based on the intentional or gross negligence of CH.</p>

                        <h5>Article 12| Changes to the rules</h5>
                        <p>1. CH may change this agreement at the discretion of CH without obtaining the prior consent of the user.</p>
                        <p>2. The changed Terms shall become effective when displayed on this site, and if points are used after the changes, it is considered that you have agreed to the changed Terms.</p>
                    </div>
                    <a href="/register" className="yellow-btn">{lang[language].close}</a>
                </div>
            </section>
            </main>
        )
    }
}

export default Privacy;