import { Component } from "react";
import lang from "../language/lang";

class Top extends Component{
    constructor(props)
    {
        super(props);
        this.state = {
            language:"en"
        }
    }
    handleChangeLanguage = (event)=>{
        localStorage.setItem("language", JSON.stringify({language:event.target.value}))
        this.setState({language:event.target.value})
        const context = this.context;
    }

    componentDidMount(){
        localStorage.setItem("language", JSON.stringify({language:"en"}))
    }
    render()
    {
       const {language} = this.state
        return(
            <main >
                <header>
                    <h2>{lang[language].firstview}</h2>
                </header>
                <section className="container" >
                    <img src="./assets/img/bg_logo.png" alt="" />
                    <a className="white-btn register-btn" href="/register">
                        <img src="./assets/img/icon_signup.png" alt="" />
                        <span>{lang[language].signup}</span>
                    </a>
                    <a className="yellow-btn register-btn" href="/login">
                        <img src="./assets/img/icon_signin.png" alt="" />
                        <span>{lang[language].login}</span>
                    </a>
                    <select onChange={this.handleChangeLanguage} className="first-select" name="language" id="language">
                        <option value="" selected disabled hidden>{lang[language].selectLanguage}</option>
                        <option value="en">{lang[language].english}</option>
                        <option value="jp">{lang[language].japanese}</option>
                    </select>
                    {/* <div class="coming" style={{overflow:"initial"}}>
                        <h3>Coming<br/>Soon!</h3>
                    </div> */}
                </section>
            </main>


        )
    }
}

export default Top;