import { Component } from "react";
import axios from 'axios';
import lang from "../language/lang";
const baseurl = process.env.REACT_APP_API_BASE_URL;


class Header extends Component{
    constructor(props)
    {
        super(props)
        this.state={
            userName:"",
            academy:"",
            daypoint:0,
            point:0,
            weekpoint:0,
            monthpoint:0,
            avatarimg:"",
            packagename:"",
            language:JSON.parse(localStorage.language).language,
        }
    }
    componentDidMount(){
        this.getProfile();
    }

    getProfile(){
        var loginuserData =  JSON.parse(localStorage.getItem("userData")) || null;
        var token = loginuserData.token
        var config = {
            method: 'get',
            url: `${baseurl}/api/getProfile`,
            headers: { 
              'Content-Type': 'application/json',
              'Authorization': 'Bearer ' + token,
            },
            data : {}
          };
          axios(config)
          .then(async (response)=>{
            var userData= response.data.user 
            this.setState({
                userName:userData.name,
                academy:userData.academy,
                packagename:userData.package,
                avatarimg:userData.avatar?`${baseurl}/media/${userData.avatar}`:"",
                daypoint:userData.daypoint.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                point:parseInt(userData.point).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                weekpoint:parseInt(userData.weekpoint).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                monthpoint:parseInt(userData.monthpoint).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
            })

          })
          .catch((error)=>{
                if (error.response) {
                    if(error.response.status===401){
                        localStorage.removeItem("userData");
                        window.location.assign('/');
                    }
                }
           });
    }

    updateAvatar = e =>{
        let fileObj = e.target.files[0];
        let avatar = URL.createObjectURL(fileObj);
        const fd = new FormData();
        fd.append('avatar', fileObj);
        var userData =  JSON.parse(localStorage.getItem("userData")) || null;
        var token = userData.token
        axios.post(`${baseurl}/api/updateAvatar`, fd, {
            headers: {
                'Authorization': 'Bearer ' + token,
            }
        }).then((response)=>{
        }).catch((error)=> {
        })      
        this.setState({
            avatarimg:avatar,
        })
    }

    langClick = event=> {
        var langSelect = document.getElementsByClassName('header-lang-select');
        if (langSelect[0].style.maxHeight) {
            langSelect[0].style.maxHeight = null;
        }
        else {
            langSelect[0].style.maxHeight = langSelect[0].scrollHeight + "px";
        }
    }

    langSelect = x => event => {
        var langSelect = document.getElementsByClassName('header-lang-select');
        if (langSelect[0].style.maxHeight) {
            langSelect[0].style.maxHeight = null;
        }
        localStorage.setItem("language", JSON.stringify({language:x}))
        this.setState({language:x});
        window.location.reload(); 
    }

    render()
    {
        const {userName, academy, point, avatarimg,language, packagename} = this.state
        return(
            <>              
                <div className="header">
                    <div className="header-img">
                        <img onClick={(e) => this.upload.click() } src={avatarimg=="" ? "/assets/img/avatar.jpg" : avatarimg} alt="" />
                        <input type="file" onChange={this.updateAvatar} ref={(ref) => this.upload = ref} style={{ display: 'none' }} multiple accept="image/png, image/gif, image/jpeg"/>
                    </div>
                    <table className="header-table">
                        <tbody>
                            <tr>
                                <td>{lang[language].userName}</td>
                                <td>{userName}</td>
                            </tr>
                            <tr>
                                <td>{lang[language].academy}</td>
                                <td>{academy}</td>
                            </tr>
                            <tr>
                                <td>{lang[language].package}</td>
                                <td>{packagename}</td>
                            </tr>
                            <tr>
                                <td>{lang[language].ownedPoint}</td>
                                <td>{point}</td>
                            </tr>
                        </tbody>
                    </table>
                    <div className="header-lang" onClick={this.langClick}>
                        <div className={language==="jp"? "header-lang-btn header-lang-jp":"header-lang-btn header-lang-us"}></div>
                        <div className="header-lang-arrow"></div>
                        <div className="header-lang-select" onClick={(e)=>{e.stopPropagation();}}>
                            <div>
                                <div onClick={this.langSelect('jp')}><img src="/assets/img/icon_lang_jpn.png" alt=""/></div>
                                <div onClick={this.langSelect('en')}><img src="/assets/img/icon_lang_usa.png" alt=""/></div>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        )
    }
}

export default Header;