import { Component } from "react";
import {BrowserRouter as Router, Route, Switch, } from 'react-router-dom';
import Mypage from './Mypage';
import Home from './Home';
import Guide from './Guide';
import Profile from './Profile';
import ProfileEdit from './ProfileEdit';
import Notification from './Notification';
import NotificationDetail from './NotificationDetail';
import CommingSoon from './CommingSoon';
import lang from "../language/lang";

class Dashboard extends Component{
    constructor(props)
    {
        super(props);
        this.state={
            activeTab:0,
            language:JSON.parse(localStorage.language).language,
        }
    }

    handleLogout = (event)=>{
        localStorage.removeItem("userData");
        window.location.assign('/');
    }

    componentDidMount(){
        var loginuserData =  JSON.parse(localStorage.getItem("userData")) || null;
        var userstatus = loginuserData.userStatus
        if(userstatus!==1)
        {
            localStorage.removeItem("userData");
            window.location.assign("/registersucess")
        }
        var activeTab = 0;
        if((window.location.pathname).split("/")[2]==="home")
            activeTab = 0;
        if((window.location.pathname).split("/")[2]==="mypage")
            activeTab = 1;
        if((window.location.pathname).split("/")[2]==="guide")
            activeTab = 2;
        if((window.location.pathname).split("/")[2]==="profile")
            activeTab = 3;
        if((window.location.pathname).split("/")[2]==="notification")
            activeTab = 4;
        if((window.location.pathname).split("/")[2]==="exchange")
            activeTab = 5;     
        this.setState({activeTab:activeTab})
    }
    
    render()
    {
        const{activeTab,language} = this.state
        
        return(
            <>
                <div className="logo logo-main">
                    <img src="/assets/img/bg_logo_black.png" alt="" />
                </div>
                <Switch>
                      <Route exact path='/dashboard/home' component={Home} />
                      <Route exact path='/dashboard/mypage' component={Mypage} />
                      <Route exact path='/dashboard/guide' component={Guide} />
                      <Route exact path='/dashboard/profile' component={Profile} />
                      <Route exact path='/dashboard/profile/edit' component={ProfileEdit} />
                      <Route exact path='/dashboard/notification' component={Notification} />
                      <Route exact path='/dashboard/notification/:id' component={NotificationDetail} />
                      <Route exact path='/dashboard/exchange' component={CommingSoon} />
                </Switch> 
                <menu>
                    <nav>
                        <ul className="menu-sp">
                            <li><a href="/dashboard/mypage">                                
                                    <div>
                                        <img src="/assets/img/icon_menu01.png" alt="" />
                                        <img src="/assets/img/icon_menu01_active.png" alt="" />
                                    </div>
                                    <span>{lang[language].myPage}</span>
                                </a>
                            </li>
                            <li>
                            <a href="/dashboard/guide">
                                    <div>
                                        <img src="/assets/img/icon_menu02.png" alt="" />
                                        <img src="/assets/img/icon_menu02_active.png" alt="" />
                                    </div>
                                    <span>{lang[language].guide}</span>
                                </a>
                            </li>
                            <li>
                                <a href="/dashboard/profile">
                                    <div>
                                        <img src="/assets/img/icon_menu03.png" alt="" />
                                        <img src="/assets/img/icon_menu03_active.png" alt="" />
                                    </div>
                                    <span>{lang[language].profile}</span>
                                </a>
                            </li>
                            <li>
                                 <a href="/dashboard/notification">
                                    <div>
                                        <img src="/assets/img/icon_menu04.png" alt="" />
                                        <img src="/assets/img/icon_menu04_active.png" alt="" />
                                    </div>
                                    <span>{lang[language].news}</span>
                                </a>
                            </li>
                            <li>
                                <a href="/dashboard/exchange">
                                    <div>
                                        <img src="/assets/img/icon_menu05.png" alt="" />
                                        <img src="/assets/img/icon_menu05_active.png" alt="" />
                                    </div>
                                    <span>{lang[language].exchange_points}</span>
                                </a>
                            </li>
                            <li>
                           
                                <a href="/dashboard/home">
                                    <img src="/assets/img/icon_menu_home.png" alt="" />
                                </a>
                                <div>
                                    <div></div>
                                </div>
                            
                            </li>
                        </ul>
                        <ul className="menu-pc">
                            <li>
                                <a href="/dashboard/home" className={activeTab==0 ? "current-page" : ""}>
                                    <div>
                                        <img src="/assets/img/icon_menu_home_pc.png" alt="" />
                                        <img src="/assets/img/icon_menu_home_active.png" alt="" />
                                    </div>
                                    <span>{lang[language].home}</span>
                                </a>
                            </li>
                            <li>
                                <a href="/dashboard/mypage" className={activeTab==1 ? "current-page" : ""}>
                                    <div>
                                        <img src="/assets/img/icon_menu01.png" alt="" />
                                        <img src="/assets/img/icon_menu01_active.png" alt="" />
                                    </div>
                                    <span>{lang[language].myPage}</span>
                                </a>
                            </li>
                            <li>
                                <a href="/dashboard/guide" className={activeTab==2 ? "current-page" : ""}>
                                    <div>
                                        <img src="/assets/img/icon_menu02.png" alt="" />
                                        <img src="/assets/img/icon_menu02_active.png" alt="" />
                                    </div>
                                    <span>{lang[language].guide}</span>
                                </a>
                            </li>
                            <li>
                                <a href="/dashboard/profile" className={activeTab==3 ? "current-page" : ""}>
                                    <div>
                                        <img src="/assets/img/icon_menu03.png" alt="" />
                                        <img src="/assets/img/icon_menu03_active.png" alt="" />
                                    </div>
                                    <span>{lang[language].profile}</span>
                                </a>
                            </li>
                            <li>
                                <a href="/dashboard/notification" className={activeTab==4 ? "current-page" : ""}>
                                    <div>
                                        <img src="/assets/img/icon_menu04.png" alt="" />
                                        <img src="/assets/img/icon_menu04_active.png" alt="" />
                                    </div>
                                    <span>{lang[language].news}</span>
                                </a>
                            </li>
                            <li>
                                <a href="/dashboard/exchange" className={activeTab==5 ? "current-page" : ""}>
                                    <div>
                                        <img src="/assets/img/icon_menu05.png" alt="" />
                                        <img src="/assets/img/icon_menu05_active.png" alt="" />
                                    </div>
                                    <span>{lang[language].exchange_points}</span>
                                </a>
                            </li>
                        </ul>
                    </nav>
                    <a onClick={this.handleLogout} className="logout-pc">{lang[language].log_out}</a>
                </menu>
            </>
        )
    }
}

export default Dashboard;