import { Component } from "react";
import axios from 'axios';
import lang from "../language/lang";
const baseurl = process.env.REACT_APP_API_BASE_URL;
class Register extends Component{
    constructor(props){
        super(props);
        this.state={
            loading:false,
            showpassword:false,
            username:"",
            email:"",
            password:"",
            academy:0,
            erroremail:"",
            errorpassword:"",
            errorTerms:"",
            errormemberID:"",
            memberID:"",
            agree:false,
            language:localStorage.getItem("language") === null ? "en" : JSON.parse(localStorage.language).language
        }
    }

    handleChange = filedName => e=>{
        
        this.setState({
            erroremail:"",
            errorpassword:"",
            errorTerms:"",
            errormemberID:"",
            [filedName]:e.target.value
        })
    }

    handleSubmit = e =>{
        e.preventDefault();
        const {username, email, password, academy, memberID, language,agree} = this.state
        if(!agree){
            this.setState({errorTerms:lang[language].error_terms});
            return
        }
        var data = JSON.stringify({"email":email,"password":password, "name":username, "academy":'RISEDOT', "memberID":memberID});
        var config = {
          method: 'post',
          url: `${baseurl}/api/signup`,
          headers: { 
            'Content-Type': 'application/json'
          },
          data : data
        };
        axios(config)
        .then((response)=>{
            this.setState({loading:false});
            this.props.history.push('/registersucess')
        })
        .catch((error)=>{
            if(error.response.data)
            {
                var errordata = error.response.data;

                if(errordata.email)
                {
                    if((errordata.email[0]).indexOf('blank')!==-1){
                        this.setState({erroremail:lang[language].require_error})
                    }
                    if((errordata.email[0]).indexOf('exists')!==-1)
                    {
                        this.setState({erroremail:lang[language].duplicate_email_error})
                    }
                    if((errordata.email[0]).indexOf('valid')!==-1)
                    {
                        this.setState({erroremail:lang[language].invalid_email_error})
                    }
                }
                if(errordata.password)
                {
                    if((errordata.password[0]).indexOf('blank')!==-1){
                        this.setState({errorpassword:lang[language].require_error})
                    }
                }
                if(errordata.memberID)
                {
                    if((errordata.memberID[0]).indexOf('exists')!==-1)
                    {
                        this.setState({errormemberID:lang[language].duplicate_memberid_error})
                    }
                   
                }
            }
        })
    }

    render()
    {
        const {username, email, password, language,  academy, agree,memberID,erroremail, errorpassword, errorTerms, errormemberID} = this.state
        return(
            <>
                <div className="logo sp">
                    <img src="./assets/img/bg_logo_black.png" alt="" />
                </div>
                <main id="register">
                    <header>
                        <h2>{lang[language].signup}</h2>
                    </header>
                    <section className="container">
                        <img src="./assets/img/bg_logo.png" alt="" className="pc" />
                        <div className="input-box">
                            <label htmlFor="">{lang[language].userName}</label>
                            <input type="text" value={username} onChange={this.handleChange("username")}/>
                        </div>
                        <div className="input-box">
                            <label htmlFor="">{lang[language].emailAddress}</label>
                            <input type="email"  value={email} onChange={this.handleChange("email")}/>
                            <span className="error">{erroremail}</span>
                        </div>
                        <div className="input-box">
                            <label htmlFor="">{lang[language].password}</label>
                            <input type="password" value={password} onChange={this.handleChange("password")}/>
                            <span className="error">{errorpassword}</span>
                        </div>
                        <div className="input-box">
                            <label htmlFor="">{lang[language].academy}</label>
                            <select value={academy} onChange={this.handleChange("academy")}>
                                <option value={"RISEDOT"}>RISEDOT</option>
                            </select>
                        </div>
                        <div className="input-box">
                            <label htmlFor="">{lang[language].userID}</label>
                            <input type="text" value={memberID} onChange={this.handleChange("memberID")}/>
                            <span className="error">{errormemberID}</span>
                        </div>
                        <div className="input-box input-checkbox">
                            <label htmlFor="privacy">
                                <input type="checkbox" id="privacy" value={agree} onChange={this.handleChange("agree")}  id="privacy"/>
                                <span></span>
                                {language==="jp"?<><a href="/privacy">利用規約</a>に同意します<div className="hissu">{lang[language].require}</div></>:<>I agree to &nbsp;<a href="/privacy"> terms of service</a>.<div className="hissu">{lang[language].require}</div></>}
                            </label>
                            
                        </div>
                        <span className="error">{errorTerms}</span>
                        <a className="yellow-btn" onClick={this.handleSubmit}>
                            <img src="./assets/img/icon_signup1.png" alt="" />
                            <span>{lang[language].submit}</span>
                        </a>
                        <p>{lang[language].haveaccount}<a href="/login">{lang[language].login}</a></p>
                    </section>
                </main>
            </>
        )
    }
}

export default Register;