import { Component } from "react";
import Header from './Header';
import axios from 'axios';
import lang from "../language/lang";
const baseurl = process.env.REACT_APP_API_BASE_URL;
const academyList = {
    jp:[
        "アカデミ",
        "アカデミ",
        "アカデミ"
    ],
    en:[
        "Risedot",
        "Risedot",
        "Risedot"
    ]
}
class ProfileEdit extends Component{
    constructor(props)
    {
        super(props)
        this.state={
            userName:"",
            academy:"",
            memberID:"",
            country:"",
            email:"",
            walletaddress:"",
            language:JSON.parse(localStorage.language).language
        }
    }

    componentDidMount(){
        this.getProfile();
    }

    getProfile(){
        var userData =  JSON.parse(localStorage.getItem("userData")) || null;
        var token = userData.token
        var config = {
            method: 'get',
            url: `${baseurl}/api/getProfile`,
            headers: { 
              'Content-Type': 'application/json',
              'Authorization': 'Bearer ' + token,
            },
            data : {}
          };
          axios(config)
          .then(async (response)=>{
                var userData= response.data.user;
                this.setState({
                    userName:userData.name,
                    academy:userData.academy,
                    memberID:userData.memberID,
                    country:userData.country,
                    email:userData.email,
                    walletaddress:userData.walletaddress
                })
                
           })
          .catch((error)=>{
           });
    }

    handleChange = filedName => e=>{
        this.setState({
            erroremail:"",
            errorpassword:"",
            [filedName]:e.target.value
        })
    }

    handleSubmit = e =>{
        var userData =  JSON.parse(localStorage.getItem("userData")) || null;
        var token = userData.token
        const{userName, academy, memberID, country, email, walletaddress} = this.state
        var data = JSON.stringify({"name":userName,"email":email, "academy":academy, "memberID":memberID, "country":country, "walletaddress":walletaddress});
        var config = {
            method: 'post',
            url: `${baseurl}/api/updateProfile`,
            headers: { 
              'Content-Type': 'application/json',
              'Authorization': 'Bearer ' + token,
            },
            data : data
          };
          axios(config)
          .then(async (response)=>{
            this.props.history.push("/dashboard/profile"); 
           })
          .catch((error)=>{
           });
    }
    handleLogout = (event)=>{
        localStorage.removeItem("adminData");
        window.location.assign('/');
    }

    langClickSp = event=> {
        var langSelect = document.getElementsByClassName('header-lang-select-sp');
        if (langSelect[0].style.maxHeight) {
            langSelect[0].style.maxHeight = null;
        }
        else {
            langSelect[0].style.maxHeight = langSelect[0].scrollHeight + "px";
        }
    }

    langSelectSp = x => event => {
        var langSelect = document.getElementsByClassName('header-lang-select-sp');
        if (langSelect[0].style.maxHeight) {
            langSelect[0].style.maxHeight = null;
        }
        localStorage.setItem("language", JSON.stringify({language:x}))
        this.setState({language:x});
        window.location.reload(); 
    }
    render()
    {
        const{userName, academy, memberID, country, email, walletaddress, language} = this.state
        return(
            <>
                <main>
                        <header>
                            <h2>{lang[language].changeinfor}</h2>
                        </header>
                        <section className={language==="jp"?"container-main":"container-main container-main-en"}>
                            <Header/>
                            <div class="container1">
                                <div class="crumb">
                                    <p><a href="/dashboard/home">{lang[language].top}</a> &nbsp;&gt;&nbsp; <a href="/dashboard/profile">{lang[language].profile}</a>&nbsp;&gt;&nbsp;{lang[language].changeinfor}</p>
                                    <div className="header-lang-sp" onClick={this.langClickSp}>
                                        <div  className={language==="jp"? "header-lang-btn-sp header-lang-jp":"header-lang-btn-sp header-lang-us"}></div>
                                        <div className="header-lang-arrow-sp"></div>
                                        <div className="header-lang-select-sp" onClick={(e)=>{e.stopPropagation();}}>
                                            <div>
                                                <div onClick={this.langSelectSp('jp')}> <img src="/assets/img/icon_lang_jpn.png" alt=""/></div>
                                                <div onClick={this.langSelectSp('en')}><img src="/assets/img/icon_lang_usa.png" alt=""/></div>
                                            </div>
                                        </div>
                                    </div>
                                    <a onClick={this.handleLogout} className="logout-sp">{lang[language].log_out}</a>
                                </div>
                                <div class="information-container">
                                    <div class="input-box">
                                        <label for="">{lang[language].userName}</label>
                                        <input type="text" value={userName} onChange={this.handleChange("userName")}  />
                                    </div>
                                    <div class="input-box">
                                        <label for="">{lang[language].academy}</label>
                                        <input type="email" value={academy} onChange={this.handleChange("academy")} disabled/>
                                    </div>
                                    <div class="input-box">
                                        <label for="">{lang[language].memberID}</label>
                                        <input type="text" value={memberID} onChange={this.handleChange("memberID")} disabled/>
                                    </div>
                                    <div class="input-box">
                                        <label for="">{lang[language].country}</label>
                                        <input type="text" value={country} onChange={this.handleChange("country")} disabled/>
                                    </div>
                                    <div class="input-box">
                                        <label for="">{lang[language].registeremail}</label>
                                        <input type="text" value={email} onChange={this.handleChange("email")} />
                                    </div>
                                    <div class="input-box">
                                        <label for="">{lang[language].walletAddress}</label>
                                        <input type="text"  value={walletaddress} onChange={this.handleChange("walletaddress")}/>
                                    </div>
                                </div>
                                <div class="information-container">
                                    <a onClick={this.handleSubmit} class="yellow-btn information-btn">
                                        <span>{lang[language].change}</span>
                                    </a>
                                </div>
                            </div>
                        </section>
                    </main>
            </>
        )
    }
}

export default ProfileEdit;