import { Component } from "react";
import axios from 'axios';
import lang from "../language/lang";
const baseurl = process.env.REACT_APP_API_BASE_URL;
async function setUserData(userdata) {
    localStorage.setItem("userData", JSON.stringify(userdata))
}
class Login extends Component{
    
    constructor(props)
    {
        super(props);
        this.state = {
            email:"",
            password:"",
            language: localStorage.getItem("language") == null ? "en" : JSON.parse(localStorage.language).language,
            alertmodal:false
        }
    }

    handleChange = filedName => e=>{
        this.setState({
            erroremail:"",
            errorpassword:"",
            [filedName]:e.target.value
        })
    }

    handleSubmit = e =>{
        const {email, password} = this.state;
        var data = JSON.stringify({"email":email,"password":password});
        var config = {
            method: 'post',
            url: `${baseurl}/api/login`,
            headers: { 
              'Content-Type': 'application/json'
            },
            data : data
          };
          axios(config)
          .then(async (response)=>{
                setUserData(response.data);
                console.log(response.data)
                if(response.data.userStatus!=1)
                {
                    // localStorage.removeItem("userData");
                    // window.location.assign("/registersucess")
                }
                else{
                    window.location.assign("/dashboard/home")
                }
           })
          .catch((error)=>{
              this.setState({alertmodal:true})
           });
    }
    
    componentDidMount(){
        if(localStorage.getItem("language") == null)
        {
            localStorage.setItem("language", JSON.stringify({language:"en"}))
        }

    }


    render()
    {
        const{email, password, language,alertmodal} = this.state
        return(
            <>
               <div className="logo sp">
                    <img src="/assets/img/bg_logo_black.png" alt="" />
                </div>
                <main id="login">
                    <header>
                        <h2>{lang[language].login}</h2>
                    </header>
                    <section className="container">
                        <img src="/assets/img/bg_logo.png" alt="" className="pc" />
                        <div className="input-box">
                            <label htmlFor="">{lang[language].emailAddress}</label>
                            <input type="email"  value={email} onChange={this.handleChange("email")} />
                        </div>
                        <div className="input-box">
                            <label htmlFor="">{lang[language].password}</label>
                            <input type="password" value={password} onChange={this.handleChange("password")}/>
                        </div>
                        <a className="yellow-btn" onClick={this.handleSubmit}>
                            <img src="/assets/img/icon_signin.png" alt="" />
                            <span>{lang[language].login}</span>
                        </a>
                        {language==="jp"?<p>パスワードを忘れた方は<a href="/resetPassword">こちら</a></p>:<p>If you forgot your password<a href="/resetPassword">here</a></p>}
                    </section>
                </main>
                <div className={alertmodal?"modal modal-show":"modal"} onClick={(e)=>{this.setState({alertmodal:false})}}>
                    <div className="modal-body" onClick="event.stopPropagation()">
                        <p>{lang[language].login_error}</p>
                    </div>
                </div>
            </>
        )
    }
}

export default Login;